@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');

body {
  margin: 0px;
  padding: 0px;
  background-color: #ffffff;
  font-family: 'Montserrat', sans-serif;
}

* {
  box-sizing: border-box;
}
html{scroll-behavior:smooth}
ul {
  list-style: none;
}

a {
  text-decoration: none;
}

p {
  line-height: 2;
}

button {
  outline: none;
  border: none;
}

input {
  outline: none;
  border: none;
}

a.logo {
  cursor: pointer;
}

#main {
  width: 100%;
  min-height: 100vh;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("images/alpine-4176830_1920.jpg");
  box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.3);
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.336);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;
  background-color: white;
}

nav ul {
  display: flex;
}

.active {
  background-color: #337ab7;
  color: white;
  box-shadow: 5px 10px 30px rgba(153, 188, 219, 0.411);
  transition: all ease 0.2s;
}

nav ul li a {
  font-family: calibri;
  height: 40px;
  line-height: 43px;
  margin: 3px;
  padding: 0px 22px;
  display: flex;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 500;
  color: black;
  letter-spacing: 1px;
  border-radius: 3px;
  transition: 0.2s ease-in-out;
}

.hey {
  font-family: calibri;
  color: black;
  font-weight: 500;
  font-size: 0.9rem;
  border-bottom: 2px solid #337ab7;
}

nav ul li a:hover {
  background-color: #337ab7;
  color: white;
  box-shadow: 5px 10px 30px rgba(153, 188, 219, 0.411);
  transition: all ease 0.2s;
}

nav .menu-btn,
.menu-icon {
  display: none;
}

.name {
  font-family: 'Montserrat';
  width: 500px;
  position: absolute;
  left: 50%;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
}

.name .details {
  color: #dadada;
}

.name h1 {
  font-family: 'Montserrat';
  font-size: 46px;
  margin: 0px;
  letter-spacing: 3px;
  color: #ffffff;
  text-shadow: 1px 3px #2e2929;
}

.header-btns {
  display: flex;
  margin-top: 40px;
  margin-left: 6.8em;
}

.cv-btn {
  width: 110px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #337ab7;
  box-shadow: 5px 10px 30px rgba(153, 188, 219, 0.411);
  border-radius: 10px;
  color: #ffffff;
}

.cv-btn1 {
  font-size: 14px;
  width: 140px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: 1px solid #ffffff;
  color: #ffffff;
  border-radius: 10px;
  margin-left: 20px;
}

.cv-btn:hover {
  background-color: #000000;
  transition: all ease 0.5s;
  color: #337ab7;
}

.cv-btn1:hover {
  background-color: #ffffff;
  transition: all ease 0.5s;
  color: #000000;
}

.f-heading h2 {
  color: #000000;
  font-size: 2.5rem;
  margin: 0px;
  padding: 0px;
}

.f-heading p,
.s-text {
  color: rgba(20, 20, 20, 0.678);
  font-size: 1rem;
  margin-top: 10px;
  padding: 30px 100px;
  text-align: center;
}

.sub-text {
  color: white;
  font-size: 1rem;
  margin-top: 10px;
  padding: 30px 100px;
  text-align: center;
}

.f-heading {
  text-align: center;
  margin-top: 60px;
  margin-bottom: -120px;
}

#features {
  margin-top: 140px;
  width: 100%;
  height: auto;
  background-color: #fff;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 5% 0px 5%;
  position: relative;
  margin-bottom: 50px;
}

.features-text {
  width: 45%;
  color: #000000;
  list-style: circle;
  margin: 50px 0 auto 0;
}

.features-text h2 {
  font-size: 2rem;
  color: #337ab7;
  font-weight: 400;
  margin: 0px;
  padding: 0px;
}

.features-text p {
  font-size: 1.2rem;
  color: #b4b4b4;

}

.features-text span {
  display: inline-block;
  font-size: 1.17em;
  margin-block-start: 0.5em;
  font-weight: bold;
}

.features-text button {
  margin-top: 20px;
  width: 140px;
  height: 40px;
  border-radius: 10px;
  border: none;
  outline: none;
  color: #FFFFFF;
  background-color: #337ab7;
  box-shadow: 5px 10px 30px rgba(153, 188, 219, 0.411);
}

.features-text button:hover {
  background-color: #000000;
  transition: all ease 0.5s;
  color: #337ab7;
  cursor: pointer;
}

.features-model {
  width: 50%;
  margin: auto;
}

.features-model img {
  width: 600px;
}

#services {

  width: 100%;
  height: auto;
  box-sizing: border-box;
  font-family: calibri;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #ccc;
}

.s-heading h2 {
  color: #000000;
  font-size: 3rem;
  margin: 0px;
  padding: 0px;
}

.s-heading p {
  color: rgba(20, 20, 20, 0.678);
  font-size: 1rem;
  margin-top: 10px;
  padding: 30px 100px;
}

.s-heading {
  text-align: center;
  margin: 30px 0px;
}

.s-box {
  background-color: #ffffff;
  width: 350px;
  height: 470px;
  margin: 20px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.1);
  position: relative;
}

.b-container {
  display: flex;
  justify-content: center;
  align-content: center;
  margin-bottom: 100px;
}

.s-b-img {
  width: 100%;
  height: 50%;

}

.s-b-img img {
  width: 100%;
  height: 100%;

}

.s-b-text {
  width: 100%;
  height: 40%;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}

.s-b-text p {
  margin: 0px;
  color: #000000;
  font-size: 1.1rem;
  font-family: calibri;
  display: block;
  display: -webkit-box;
  max-width: 80%;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.5;
}

.s-b-text p:hover {
  color: #337ab7;
}

.s-b-text a {
  margin-top: 15px;
}

.s-type {
  font-family: calibri;
  color: #000266;
  background-color: #ffffff;
  padding: 10px 15px;
  position: absolute;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 18px;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.2);
}

.s-box:hover {
  transform: translateY(-10px);
  transition: all ease 0.3s;

}

#subscribe {
  background-color: #337ab7;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0px 0px;
  padding: 10px 0px;
}

#subscribe h2 {
  font-size: 2.5rem;
  margin: 20px;
  color: #000000;
}

#contact {
  margin-top: 140px;
  width: 100%;

  background-color: #ffffff;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 50px 5% 0px 5%;
  position: relative;
  flex-direction: column;
}

.contact-model {
  display: flex;
  margin: 50px 0 50px 0;
}

.contact-text {
  padding: 50px;
  margin: 0;
}

.col-2 {
  flex-direction: row-reverse;
  background-color: #ccc;
}

.col-2 p {
  color: rgba(20, 20, 20, 0.678);
}

.col-2 img {
  float: right;

}



@media(max-width:1190px) {
  #main {
    background-size: 1150px !important;

  }

  .name {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .header-btns {
    margin-left: 10em;
  }

  .features-model img {
    height: 400px;
  }

  #services {
    height: auto;
  }

  .b-container {
    flex-wrap: wrap;
  }

  .s-box {
    flex-grow: 1;
  }

  .s-b-img img {
    object-fit: cover;
  }

}

@media(max-width:1100px) {
  .menu-icon {
    display: block;
  }

  nav {
    justify-content: space-between;
    height: 65px;
    padding: 0px 30px;
  }

  .header-btns {
    margin: 0;
    justify-content: center;
  }

  .menu {
    display: none;
    position: absolute;
    top: 65px;
    left: 0px;
    background-color: #ffffff;
    border-bottom: 4px solid #337ab7;
    width: 100%;
    padding: 0px;
    margin: 0px;
  }

  .menu li {
    width: 100%;
  }

  nav .menu li a {
    width: 100%;
    height: 40px;
    justify-content: center;
    align-items: center;
    margin: 0px;
    padding: 25px;
    border: 1px solid rgba(158, 155, 0, 0.03);
  }

  nav .menu-icon {
    cursor: pointer;
    float: right;
    padding: 28px 20px;
    position: relative;
    user-select: none;
  }

  nav .menu-icon .nav-icon {
    background-color: #337ab7;
    position: absolute;
    display: block;
    height: 2px;
    position: relative;
    transition: background 0.2s ease-out;
    width: 18px;
  }

  nav .menu-icon .nav-icon:before,
  nav .menu-icon .nav-icon:after {
    background: #337ab7;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all ease-out 0.2s;
    width: 100%;
  }

  nav .menu-icon .nav-icon:before {
    top: 5px;
  }

  nav .menu-icon .nav-icon:after {
    top: -5px;
  }

  nav .menu-btn:checked~.menu-icon .nav-icon {
    background: transparent;
  }

  nav .menu-btn:checked~.menu-icon .nav-icon:before {
    transform: rotate(-45deg);
    top: 0;
  }

  nav .menu-btn:checked~.menu-icon .nav-icon:after {
    transform: rotate(45deg);
    top: 0;
  }

  nav .menu-btn {
    display: none;
  }

  nav .menu-btn:checked~.menu {
    display: block;
  }

  .name {
    width: 65%;
  }

  #features,
  .contact-model {
    flex-direction: column;
  }

  .contact-model {
    margin: auto;
  }

  .contact-text {
    width: auto;
    margin: 0 auto;
  }

  .col-2 img {
    float: none;
  }

  #contact {
    margin-top: 0;
  }

  .features-model {
    width: 100%;
    margin: 0;
    text-align: center;
    padding-top: 60px;
  }

  .features-text {
    width: 90%;
    text-align: center;
  }
}

@media(max-width:970px) {
  #main {
    background-size: auto !important;
  }

  #services {
    margin-top: -60px;
  }

  .s-heading h2 {
    font-size: 2.5rem;
  }

  .features-text h2 {
    font-weight: 600;
  }

  .b-container {
    margin-top: 30px;
  }

  #subscribe h2 {
    font-size: 2.5rem;
  }

  #features {
    margin-top: -60px;
    justify-content: center;
    padding-top: 0px;
    font-size: 2rem;
    text-align: center;
  }

  .features-text {
    width: 90%;
    margin-bottom: 50px;
  }

  .f-heading {
    display: none;
  }

  .features-text h1 {
    font-size: 4rem;
  }

  #contact {
    margin: 0;
  }

  #contact .contact-model {
    margin: 0;
    justify-content: center;
    padding-bottom: 60px;
    font-size: 2rem;
    text-align: center;
  }

}

@media(max-width:600px) {
  #main {
    background-size: 600px !important;
    background-image: url("images/forest-mobile.jpg");
  }

  .logo img {
    width: 150px;
  }

  .features-model img {
    width: auto;
    height: 230px;
  }

  .name {
    width: 100%;
  }

  .name h1 {
    font-size: 2em;
    text-align: center;
  }

  #services,
  #subscribe {
    padding: 5%;
  }

  .s-heading p,
  .s-text {
    padding: 0;
    font-size: 1.2rem;
    font-family: 'Montserrat', sans-serif;
  }

  #contact .contact-model,
  #contact {
    padding: 0;
  }

  .contact-text {
    padding: 0;
    margin: 50px auto;
  }

}

.scroll {
  left: 42.5%;
}


@media(max-width:400px) {
  .s-box {
    width: 100%;
    height: 400px;
  }
}

@media(max-width:376px) {
  .subscribe-input {
    width: 300px;
    height: 50px;
  }

  nav .menu-icon {
    position: absolute;
    margin-left: 34.5%;
  }
}